import React from 'react';

import myPhoto from '../../images/meCV.jpg';

const Header = () => {
    return (
        <div>
            <div className='header-container'>
                <ul className='row'>
                    <div className='col-md-3'>
                        <img src={myPhoto} alt='My Photo' className='myPhoto' />
                    </div>
                    <div className='col-md-9'>
                        <p className='header-text'>Jestem studentem Polsko-Japońskiej Akademii Technik Komputerowych. Studiuję informatykę na ostatnim semestrze inżynierki. W tej chwili realizuję pierwszy projekt komercyjny jaką FullStack programista. W projekcie używam MERN stack. Jestem ambitny, otwarty do nowych technologii. Szukam pracy jaką junior front-end developer, albo junior javascript developer na terenie Warszawy.</p>
                    </div>
                </ul>
            </div>
            <div className='name-container'>
                <h1>Vladyslav Lysak</h1>
            </div>
        </div>
    )
}
export default Header;
