import React from 'react';

import greyCircle from '../images/grey.png';
import blackCircle from '../images/black.png';

const getCircleElement = (image = greyCircle) => (<div className='circle-padding'>
  <img src={image} alt='circle' className='circle' />
</div >)

const createEmptyCircleArray = (maxCount = 5) => {
  let arr = [];
  for (let i = 0; i < maxCount; i++) {
    arr.push(getCircleElement(greyCircle));
  }
  return arr;
}

const Circles = ({ count }) => {
  if(count > 5) {
    count = 5;
  }
  let circles = createEmptyCircleArray();

  for (let i = 0; i < count; i++) {
    circles[i] = getCircleElement(blackCircle)
  }

  return (
    <div className='circle-container'>
      {circles.map(circle => circle)}
    </div>
  )
}

export default Circles;
